import { createRouter, createWebHistory } from 'vue-router'
import HomeMenu from '../components/HomeMenu.vue'
import Games from '../views/GamesList.vue'
import Impact9Game from '../views/Impact9Game.vue'
import Black8Jack from '../views/Black8Jack.vue'

const routes = [
  {
    path: '/',
    name: 'Home',
    component: HomeMenu
  },
  {
    path: '/games',
    name: 'Games',
    component: Games
  },
  {
    path: '/Impact9Game',
    name: 'Impact9Game',
    component: Impact9Game
  },
  {
    path: '/Black8Jack',
    name: 'Black8Jack',
    component: Black8Jack
  },
]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes
})

export default router