<template>
    <div class="black8jackContainer">
      <h2>Black8Jack</h2>
  
      <!-- Coin flip section -->
      <div v-if="!gameStarted && !result" class="coinContainer">
        <h3>Déterminer l'équipe qui commence</h3>
        <h3>Lancer la pièce !</h3>
        <div class="coin" :class="{ flipping: isFlipping }" @click="flipCoin">
          <div class="face front"><img class="piece" src="@/assets/pile.png" /></div>
          <div class="face back"><img class="piece" src="@/assets/face.png" /></div>
        </div>
      </div>
  
      <!-- Game start -->
      <div v-if="result && !gameStarted" class="coinResult">
        <p>{{ result }} commence le jeu !</p>
        <div class="butContainer">
          <button @click="startGame" class="btn-start">Commencer</button>
        </div>
      </div>
  
      <!-- Game container -->
      <div v-if="gameStarted" class="gameContainer">
      <div class="teams">
        <div
          v-for="(team, teamName) in teams"
          :key="teamName"
          :class="['team', { active: currentTeam === teamName }]"
        >
          <h3>Équipe {{ teamName === 'team1' ? '1' : '2' }}</h3>
          <ul>
            <li v-for="player in team" :key="player.name">
              {{ player.name }}
            </li>
          </ul>
          <p class="team-score">Points d'équipe : {{ scores[teamName] }}</p>
        </div>
      </div>

      <p class="currentTurn">
        C'est au tour de l'équipe {{ currentTeam === 'team1' ? '1' : '2' }}.
        Rentrer des boules pour accumuler des points !
      </p>
  
        <!-- Balls section -->
        <div class="balls">
          <div class="numberedBalls">
            <img
              v-for="n in 15"
              :key="n"
              :src="getImagePath(n)"
              :alt="`Boule ${n}`"
              class="ball"
              :class="{ selected: selectedBalls.includes(n), disabled: usedBalls.includes(n) }"
              @click="toggleBallSelection(n)"
            />
          </div>
          <button 
            @click="endTurn" 
            class="btn-end-turn"
            :disabled="selectedBalls.length === 0"
          >
            Terminer le tour
          </button>
        </div>
      </div>
  
      <!-- Popup for winner -->
      <div v-if="showTurnPopup" class="popup-overlay">
        <div class="popup-content">
          <template v-if="winner">
            <h3>Félicitations !</h3>
            <p>L'équipe {{ winner === 'team1' ? '1' : '2' }} remporte la partie !</p>
            <button @click="goToHome" class="btn-start">OK</button>
          </template>
          <template v-else>
            <h3>Changement de tour</h3>
            <p>C'est au tour de l'équipe {{ currentTeam === 'team1' ? '1' : '2' }}</p>
            <button @click="closeTurnPopup" class="btn-start">OK</button>
          </template>
        </div>
      </div>
    </div>
  </template>
  
  
  
  
  <script>


export default {
  name: "Black8JackGame",
  data() {
    return {
      players: [],
      teams: { team1: [], team2: [] },
      isFlipping: false,
      result: "",
      gameStarted: false,
      scores: { team1: 0, team2: 0 },
      currentTeam: null,
      selectedBalls: [],
      usedBalls: [],
      showTurnPopup: false,
      maxPoints: 60,
      winner: null,
    };
  },
  created() {
    const savedPlayers = JSON.parse(localStorage.getItem("players")) || [];
    this.players = savedPlayers.map(player => ({
      name: player.name || player,
      score: player.score || 0,
    }));
    this.createTeams();
  },
  methods: {
    goToHome() {
      this.$router.push("/");
    },
    getImagePath(n) {
      return require(`@/assets/ball_${n}.png`);
    },
    createTeams() {
      const shuffledPlayers = [...this.players].sort(() => Math.random() - 0.5);
      const mid = Math.ceil(shuffledPlayers.length / 2);
      this.teams.team1 = shuffledPlayers.slice(0, mid);
      this.teams.team2 = shuffledPlayers.slice(mid);
    },
    flipCoin() {
      if (this.isFlipping) return;
      this.isFlipping = true;
      setTimeout(() => {
        const random = Math.random() < 0.5 ? "Équipe 1" : "Équipe 2";
        this.result = random;
        this.currentTeam = random === "Équipe 1" ? "team1" : "team2";
        this.isFlipping = false;
      }, 2000);
    },
    startGame() {
      this.gameStarted = true;
    },
    toggleBallSelection(ballNumber) {
      if (this.usedBalls.includes(ballNumber)) return;
      const index = this.selectedBalls.indexOf(ballNumber);
      if (index === -1) this.selectedBalls.push(ballNumber);
      else this.selectedBalls.splice(index, 1);
    },
    endTurn() {
      // Calculer les points des boules sélectionnées (excluant la boule 8)
      const regularBalls = this.selectedBalls.filter(ball => ball !== 8);
      const points = regularBalls.reduce((sum, ballNumber) => sum + ballNumber, 0);
      
      // Ajouter les points des boules normales
      this.scores[this.currentTeam] += points;

      // Vérifier si l'équipe empoche la boule 8
      const isEightBall = this.selectedBalls.includes(8);

      // Gérer les différents scénarios avec la boule 8
      if (isEightBall) {
        // Scénario 1: L'équipe a moins de 52 points et empoche la 8
        if (this.scores[this.currentTeam] < 52) {
          this.scores[this.currentTeam] = -1; // L'équipe perd
          this.winner = this.currentTeam === "team1" ? "team2" : "team1";
          this.showTurnPopup = true;
          return;
        }
        // Scénario 2: L'équipe a 52 points ou plus et empoche la 8
        else if (this.scores[this.currentTeam] >= 52) {
          this.scores[this.currentTeam] += 8; // Ajouter 8 points
          if (this.scores[this.currentTeam] >= this.maxPoints) {
            this.winner = this.currentTeam;
            this.showTurnPopup = true;
            return;
          }
        }
      }

      // Enregistrer les boules utilisées
      this.usedBalls.push(...this.selectedBalls);

      // Vérifier la victoire sans la boule 8
      if (this.scores[this.currentTeam] >= this.maxPoints) {
        this.winner = this.currentTeam;
        this.showTurnPopup = true;
        return;
      }

      // Passer au tour suivant
      this.currentTeam = this.currentTeam === "team1" ? "team2" : "team1";
      this.selectedBalls = [];
      this.showTurnPopup = true;

      // Mettre à jour l'affichage des scores
      this.updateScoreDisplay();
    },
    updateScoreDisplay() {
      // Les scores sont automatiquement mis à jour dans le template
      // grâce à la réactivité de Vue
      // Mais on peut ajouter des logs pour le debugging
      console.log(`Score Team 1: ${this.scores.team1}`);
      console.log(`Score Team 2: ${this.scores.team2}`);
    },
    closeTurnPopup() {
      this.showTurnPopup = false;
    },
  },
};
  </script>
  
  
