.coin {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  background: linear-gradient(145deg, #ffffff, #d1d1d1);
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  position: relative;
  perspective: 1000px;
  transition: transform 0.5s ease;
}

.coin.flipping {
  animation: flip 2s ease infinite;
}

.face {
  position: absolute;
  width: 100%;
  height: 100%;
  border-radius: 50%;
  backface-visibility: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
}

.front {
  transform: rotateY(0deg);
}

.back {
  transform: rotateY(180deg);
}

@keyframes flip {
  0% {
    transform: rotateY(0deg);
  }
  50% {
